import CouponItme from '../../components/CouponItme/CouponItme.vue';
import CouponItmeEnd from '../../components/CouponItmeEnd/CouponItmeEnd.vue';
export default {
	components: {
		CouponItme,
		CouponItmeEnd
	},
	data() {
		return {
			couponValidList: [],
			couponinvalidList: [],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'valid',
			subState: '',
			theIndex: null,
			oldIndex: null,
			isStop: false
		}
	},
	onPageScroll(e) {

	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		this.getCouponByUser()
	},
	methods: {
		switchType(type) {
			if (this.typeClass == type) {
				return;
			}
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			})
			this.typeClass = type;
			this.subState = this.typeClass == '' ? '' : 'show' + type;
			setTimeout(() => {
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass == 'valid' ? '' : this.subState;
			}, 200)
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},

		touchEnd(index, $event) {
			//解除禁止触发状态
			this.isStop = false;
		},


		deleteCoupon(id) {
			//删除优惠卷记录
			this.$api.deleteCoupon({
				couponId: id
			}, res => {
				if (res.code) {
					this.$common.successToShow("删除成功")
					this.getCouponByUser()
				}
			})
		},

		discard() {
			//丢弃
		},
		//获取优惠卷信息
		getCouponByUser() {
			this.$api.getCouponByUser({}, (res) => {
				if (res.code) {
					this.couponValidList = res.data.couponValidList;
					this.couponinvalidList = res.data.couponinvalidList;
				}
			})
		},
		CouponClick(goodsId) {
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsId=' + goodsId
			})
		}
	}
}
